import React from "react"
import { CCol, CCard, CCardBody, CButton, CFormInput, CFormLabel, CSpinner } from "@coreui/react";
import { useParams } from "react-router-dom";
import Constants, { userToken } from "src/Constants";

const CameraDetails = () => {

    const { id } = useParams();
    const [body, setBody] = React.useState({ start: '', end: '' })
    const [data, setData] = React.useState({});
    const [num, setNum] = React.useState(64)
    const scrollBoxRef = React.useRef()

    React.useEffect(() => {
        (async () => {
            // console.log(body)

            let url = Constants.singleCamera + id + `?per_page=${num}`;
            let response = await fetch(url, {
                method: 'POST',
                headers: {
                    'Authorization': userToken(),
                    'Content-Type': 'application/json'

                },
                body: JSON.stringify(body)
            })
            let data = await response.json();
            setData(data.attributes[0]);
        })();
    }, [id, body, num])

    const handleSearch = (e) => {
        e.preventDefault();

        let formData = new FormData(e.target)
        let start = new Date(formData.get('start')).toISOString()
        let end = new Date(formData.get('end')).toISOString()
        setBody({ start, end })


    }
    React.useEffect(() => {
        let handleLazyLoad = (e) => {
            let rowheight = scrollBoxRef.current?.scrollHeight;
            if (!rowheight) return;
            if (window.scrollY + window.innerHeight >= rowheight) {
                setNum((p) => p + 64)
            }
        }
        document.addEventListener('scroll', handleLazyLoad)

        return () => {
            document.removeEventListener('scroll', handleLazyLoad)
        }
    }, [])
    if (!Object.keys(data).length) {
        return <CSpinner color="primary" />
    }
    return (
        <>
            <CCard className="my-2">
                <CCardBody>
                    <div className="row">
                        <div className="col-md-6">
                            <h5>Display Name: {data.name}</h5>
                            <h5>Address: {data.address}</h5>
                        </div>
                        <div className="col-md-6">
                            <h5 className="float-end">Camera Id: {data.unique_id}</h5>
                            <h5 className="float-end">Latest Image Captured At: {data?.images?.[0]?.created_at ? new Date(data?.images?.[0]?.created_at).toLocaleString() : 'No Image Available'}</h5>
                        </div>
                    </div>
                </CCardBody>
            </CCard>

            <CCard className="my-2">
                <CCardBody>
                    <form className="row" onSubmit={handleSearch}>
                        <div className="col-5">
                            <CFormLabel>Start DateTime</CFormLabel>
                            <CFormInput type="datetime-local" className="search-text" name="start" />
                        </div>
                        <div className="col-5">
                            <CFormLabel>End DateTime</CFormLabel>
                            <CFormInput type="datetime-local" className="search-text" name="end" />
                        </div>
                        <div className="col-2">
                            <CFormLabel style={{ opacity: 0 }}>Search</CFormLabel>
                            <CButton type="submit" className="float-end w-100" color="primary">Search</CButton>
                        </div>
                    </form>
                </CCardBody>

            </CCard>

            <div className="row" ref={scrollBoxRef}>

                {
                    data.images?.map((image, index) => {
                        return (
                            <CCol xs={6} key={index}>
                                <CCard>
                                    <CCardBody className="camera-feed">
                                        {/* <p className="float-start">Temparature: 95<span>&#176;</span>F</p> */}
                                        <p className="float-end">Captured: {new Date(image.created_at).toLocaleString()}</p>
                                        <a className="link-dark m-2" href={`${Constants.base}uploads/${image.image_name}`} target="_blank" rel="noreferrer"> <img className="img-fluid" loading="lazy" src={`${Constants.base}uploads/${image.image_name}`} alt={image.image_name} /></a>
                                    </CCardBody>
                                </CCard>
                            </CCol>
                        )
                    })

                }
                {!data.images.length ? 'No Images are available' : ''}
            </div>

        </>
    )
}
export default CameraDetails